*{
  box-sizing: border-box; 
}
body {
  margin: 0;
  background: #e9ebee;
  color: #1d2129;
}

.Navbar{
  color: #ffffff;
  padding: 10px;
  background-color: #4167b2;
  
}

.Input, .Post{
  border: 1px solid hsl(216, 8%, 88%);
  border-radius: 4px;
  width: 576px;
  margin: 20px auto;
  background-color: #ffffff ;
}

.Input_header{
  background-color: #f5f6f7 ;
  border-bottom: 1px solid #dddfe2;
  border-radius: 2px 2px 0 0;
  margin: 0;
  padding: 15px;
}

.Input_field{
  width: 100%;
  height: 100px;
  padding: 10px;
  font-size: 20px;
  background-color:#ffffff ;
  border: none;
}
.Input_field:focus{
  outline: none;
}

.Post__image{
  width: 100%;
  margin-top: 15px;
}